import React from 'react';
import DropdownButton from 'react-bootstrap/DropdownButton';
import Dropdown from 'react-bootstrap/Dropdown';



class Cam extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedAutobahn: "Keine",
            autobahnliste: [],
            autobahncamjsons: []
        };

        
    }

    componentDidMount(){
        
        // holen aller Autobahnen, die in der API gelistet sind.
            fetch("https://verkehr.autobahn.de/o/autobahn")
            .then(response=>response.json())
            .then(data=>this.setState({autobahnliste: Array.from(data.roads)}))
            .catch(e=>this.setState({autobahnliste:"FEHLER" + e}));
            
        
    }

    handleAutobahnOnClick(myitem){
        console.log("klick " + myitem.item)
       this.setState({selectedAutobahn: myitem.item})
        fetch("https://verkehr.autobahn.de/o/autobahn/"+myitem.item+"/services/webcam")
       .then(response=>response.json())
       .then(data=>this.setState({autobahncamjsons: data.webcam}))
    }

    render() {
        return (
            
            <div className="Cam">
                <div id="ASelec">{this.state.selectedAutobahn}</div>
                <div id="myDDM">
                <DropdownButton id="dropdown-item-button" title="Autobahn Auswahl">
                {
                    this.state.autobahnliste.map(item=>{
                        return(<Dropdown.Item key={item} onClick={()=>this.handleAutobahnOnClick({item})}>{item}</Dropdown.Item>);
                    })
                 }               
                </DropdownButton>           
                </div>
                <div id="insert"></div>
                {/* wie oben jeweils einen VideoView erstellen, nach dem die liste der Autobahnen abegrufen wurde */}
                <div className="mycam-sectiontable">
                    {this.state.autobahncamjsons.map(cam=>{
                        return(<div className="mycams-video-heading" id={cam.identifier}>
                                <div className="mycams-video">
                                <iframe title="mytitle" src={cam.linkurl}></iframe>
                                </div>
                            </div>)
                    })
                    }
                </div>
            </div>
        );
    }
}
export default Cam;
